/**
 * @param colorScale
 * @param mode
 */
export function setColorScaleDomain (colorScale, mode) {
  // TODO : Set domain of color scale
  const max = (mode === 0 ? 15 : 7.44)
  const min = (mode === 0 ? 0 : -1)
  colorScale.domain([min, max])
}

/**
 * @param svg
 */
export function showSuccCount (svg) {
  let count = 0
  d3.selectAll('#map-viz circle').each(function (d) {
    if (d3.select(this).style('visibility') !== 'hidden') {
      count++
    }
  })
  d3.select('#count')
    .text('Nombre de succursales affichées: ' + count)
}

/**
 * @param currentZoom
 */
export function unselectSucc (currentZoom) {
  d3.select('#map-viz .selected')
    .transition()
    .duration('125')
    .attr('r', 7 / currentZoom.val)
    .style('stroke-width', 1 / currentZoom.val)
  d3.select('#map-viz .selected')
    .style('fill', function (d) {
      switch (d.type) {
        case 'SAQ': return 'red'
        case 'SAQ Sélection': return 'blue'
        case 'SAQ Express': return 'orange'
        case 'SAQ Dépôt': return 'purple'
        case 'SAQ Restauration': return 'yellow'
      }
    })
    .classed('selected', false)

  d3.selectAll('#map-viz .cancel')
    .style('cursor', 'default')

  d3.select('#map-panel')
    .style('visibility', 'hidden')
}

/**
 * @param child
 * @param path
 * @param data
 * @param colorScale
 * @param currentZoom
 */
export function drawMap (child, path, data, colorScale, currentZoom) {
  child.append('g')
    .selectAll('#map-viz path')
    .data(data.features)
    .enter()
    .append('g')
    .append('path')
    .attr('d', path)
    .style('fill', function (d, i) {
      if (i !== 0) {
        return colorScale(d.properties.succ_par_hab)
      }
    })
    .style('stroke', 'white')
    .style('stroke-width', 1 / currentZoom.val)
    .attr('class', 'cancel')
    .on('mouseover', function (d) {
      /*
      child
        .append('g')
        .append('text')
        .attr('class', 'mapLabel')
        .attr('x', path.centroid(d)[0])
        .attr('y', path.centroid(d)[1])
        .attr('width', '100')
        .attr('height', '30')
        .style('text-anchor', 'middle')
        .style('font-size', 12 / currentZoom.val)
        .text(d.properties.res_nm_reg)
        */

      d3.select('#map-viz svg')
        .append('rect')
        .attr('x', 1250)
        .attr('y', 675)
        .attr('rx', 12)
        .attr('ry', 12)
        .attr('width', 300)
        .attr('height', 150)
        .attr('fill', 'rgba(200,200,200,0.75)')
        .attr('class', 'region-info-bg')

      d3.select('#map-viz svg')
        .append('text')
        .attr('x', 1260)
        .attr('y', 725)
        .attr('class', 'region-info')
        .style('text-anchor', 'left')
        .style('font-size', 20)
        .text(d.properties.res_nm_reg)

      if (d3.select('#switch-map').property('value') === 'Switch 1') {
        d3.select('#map-viz svg')
          .append('text')
          .attr('x', 1260)
          .attr('y', 785)
          .attr('class', 'region-info')
          .style('text-anchor', 'left')
          .style('font-size', 15)
          .text('- ' + d.properties.succ_par_hab + ' succursales/100 000 hab.')
      } else {
        d3.select('#map-viz svg')
          .append('text')
          .attr('x', 1260)
          .attr('y', 785)
          .attr('class', 'region-info')
          .style('text-anchor', 'left')
          .style('font-size', 15)
          .text('- ' + d.properties.succ_par_km2 + ' succursales/10 000 km2')
      }

      d3.select('#map-viz svg')
        .append('text')
        .attr('x', 1260)
        .attr('y', 755)
        .attr('class', 'region-info')
        .style('text-anchor', 'left')
        .style('font-size', 15)
        .text('- ' + d.properties.nb_succ + ' succursales')

      d3.select(this)
        .style('stroke-width', 3 / currentZoom.val)
        .classed('hovered', true)
    })
    .on('mouseout', function (d) {
      d3.selectAll('#map-viz .region-info').remove()
      d3.select('#map-viz .region-info-bg').remove()
      // d3.selectAll('#map-viz .mapLabel').remove()
      d3.select(this)
        .classed('hovered', false)
        .style('stroke-width', 1 / currentZoom.val)
    })
    .on('click', function (d) {
      unselectSucc(currentZoom)
    })
}

/**
 * @param currentZoom
 */
export function filtering (currentZoom) {
  d3.selectAll('#map-viz .ui-checkbox')
    .on('click', function (d) {
      const label = d3.select(this).select('label')
      const checkbox = d3.select(this).select('input')

      const type = checkbox.property('value')
      console.log(d3.select(this))

      if (label.classed('ui-checkbox-on') === true) {
        if (checkbox.property('value') === 'tout') {
          d3.selectAll('#map-viz .marker')
            .style('visibility', 'hidden')

          // $('#map-viz .marker').css('visibility', 'hidden')

          unselectSucc(currentZoom)

          // console.log($('#map-viz input[type=checkbox]')[0].prop('checked'))

          /*
          $('#map-viz input[type=checkbox]').attr('checked', function (i) {
            if (i !== 0) {
              return false
            }
          })
          */
          d3.selectAll('#map-viz .ui-checkbox label').classed('ui-checkbox-off', true)
          d3.selectAll('#map-viz .ui-checkbox label').classed('ui-checkbox-on', false)
          d3.selectAll('#map-viz .ui-checkbox input').attr('data-cacheval', function (d, i) { if (i !== 0) return true })
          label.classed('ui-checkbox-off', false)
          label.classed('ui-checkbox-on', true)
        } else {
          d3.selectAll('#map-viz .marker')
            .style('visibility', function (d) {
              if (d.type === type) {
                if (d3.select(this).classed('selected')) {
                  unselectSucc(currentZoom)
                }
                return 'hidden'
              } else {
                return d3.select(this).style('visibility')
              }
            })
        }
      }
      if (label.classed('ui-checkbox-off') === true) {
        if (checkbox.property('value') === 'tout') {
          d3.selectAll('#map-viz .marker')
            .style('visibility', 'visible')

          d3.selectAll('#map-viz .ui-checkbox label').classed('ui-checkbox-off', false)
          d3.selectAll('#map-viz .ui-checkbox label').classed('ui-checkbox-on', true)
          d3.selectAll('#map-viz .ui-checkbox input').attr('data-cacheval', function (d, i) { if (i !== 0) return false })
          label.classed('ui-checkbox-off', true)
          label.classed('ui-checkbox-on', false)
        } else {
          d3.selectAll('#map-viz .marker')
            .style('visibility', function (d) {
              if (d.type === type) {
                return 'visible'
              } else {
                return d3.select(this).style('visibility')
              }
            })
        }
      }

      showSuccCount(d3.select('#map-viz svg'))
    })
}

/**
 * @param child
 * @param data
 * @param panel
 * @param currentZoom
 */
export function addMapMarkers (child, data, panel, currentZoom) {
  child.append('g')
    .selectAll('#map-viz circle')
    .data(data)
    .enter()
    .append('circle')
    .attr('cx', function (d) {
      return d.x
    })
    .attr('cy', function (d) {
      return d.y
    })
    .attr('r', 7)
    .attr('class', 'marker')
    .style('fill', function (d) {
      switch (d.type) {
        case 'SAQ': return 'red'
        case 'SAQ Sélection': return 'blue'
        case 'SAQ Express': return 'orange'
        case 'SAQ Dépôt': return 'purple'
        case 'SAQ Restauration': return 'yellow'
      }
    })
    .style('visibility', 'visible')
    .style('stroke', 'white')
    .style('stroke-width', 1)
    .on('click', function (d) {
      unselectSucc(currentZoom)
      panel.display(d)

      d3.select(this)
        .transition()
        .duration(200)
        .attr('r', 15 / currentZoom.val)
        .style('stroke-width', 3 / currentZoom.val)

      d3.select(this)
        .classed('selected', true)

      d3.selectAll('#map-viz .cancel')
        .style('cursor', 'not-allowed')
    })
    .on('mouseover', function (d) {
      if (d3.select(this).classed('selected') === false) {
        d3.select(this)
          .classed('hovered', true)
        d3.select(this)
          .transition()
          .duration(200)
          .style('stroke-width', 2 / currentZoom.val)
          .attr('r', 10 / currentZoom.val)
      }
    })
    .on('mouseout', function (d) {
      if (d3.select(this).classed('selected') !== true) {
        d3.select(this)
          .transition()
          .duration(125)
          .style('stroke-width', 1 / currentZoom.val)
          .attr('r', 7 / currentZoom.val)
      }
      d3.select(this)
        .classed('hovered', false)
    })
}
