  /**
 * Generates the Sankey object and add nodes and links.
 */
export function createSankey (data, width, height) {
    return d3.sankey()
    .nodeWidth(20)
    .nodePadding(8)
    .nodeAlign(d3.sankeyLeft)
    .linkSort((d1, d2) => d1.source.name - d2.source.name)
    .extent([[1, 1], [width - 1, height - 6]])
    .iterations(32);
}

  /**
 * Draw the links of the sankey on the canvas
 */
   export function drawSankeyLinks (g) {
   return g.append("g")
    .attr("class", "links")
    .attr("fill", "none")
    .attr("stroke", "#000")
    .attr("stroke-opacity", 0.2)
    .selectAll("path");
}

  /**
 * Draw the nodes of the sankey on the canvas
 */
   export function drawSankeyNodes(g) {
    return g.append("g")
    .attr("class", "nodes")
    .attr("font-family", "sans-serif")
    .attr("font-size", 11)
    .selectAll("g");
}

  /**
 * Generates the colored rectangles for the nodes
 */
   export function drawSankeyNodesRects(node, secondCategoryWithChild, func) {
    node.append("rect")
    .attr("x", function(d) { return d.x0; })
    .attr("y", function(d) { return d.y0; })
    .attr("height", function(d) { return d.y1 - d.y0; })
    .attr("width", function(d) { return d.x1 - d.x0; })
    .attr("fill", function(d) { 
        return  secondCategoryWithChild.has(d.name)? "#7F006E": "#E7D1D4"; })
    .attr("stroke", "#000")
    .on('click', function(d) {
        func(d.name)
    });
}

  /**
 * Add a label for the note
 */
   export function addSankeyNodeLabels(node) {
    node.append("rect")
    .attr("x", function(d) { return d.x1 + 3; })
    .attr("y", function(d) { return (d.y1 + d.y0) / 2 - 8; })
    .style("fill", d3.color("white") )
    .style("fill-opacity", "6 0%")
    .style("filter", "url(#f1)")
    .attr("width", function(d) {
        let canvas = document.createElement("canvas");
        let context = canvas.getContext("2d");
        context.font = "11px sans-serif";
        let width = context.measureText(d.name).width;
        return width + 4})
    .attr("height", "13")


    node.append("text")
    .attr("x", function(d) { return d.x0 - 6; })
    .attr("y", function(d) { return (d.y1 + d.y0) / 2; })
    .attr("dy", "0.35em")
    .attr("text-anchor", "end")
    .text(function(d) { return d.name; })
    .attr("x", function(d) { return d.x1 + 5; })
    .attr("text-anchor", "start");
}
