/**
 * Displays the information panel when a marker is clicked.
 *
 * @param {object} d The data bound to the clicked marker
 * @param {*} color The color scale used to select the title's color
 */
export function display (d, sankeyParametersArray, firstCategories) {
  const panel = d3.select('#sankey-panel').style('visibility', 'visible')
  panel.selectAll('*').remove()

  const title = panel
    .append('div')
    .style('font-size', '24px')
    .style('font-weight', 'bold')
    .style('margin-bottom', '10px')

  setTitle(title, d)

  const distinctProduct = panel
    .append('div')
    .style('font-size', '16px')
    .text("Nombre de produits différents: ")

    let succ1Name
    let succ1Quantity
    if (sankeyParametersArray[0].regionFilter == "all" && sankeyParametersArray[0].branchFilter == "all") {
        succ1Name = "Toutes les régions"
        if (firstCategories.has(d.name)) {
            succ1Quantity = sankeyParametersArray[0].data.find((e) => e.region == "all" && e.succ_name == "all" && e.source == d.name).value
        } else {
            succ1Quantity = sankeyParametersArray[0].data.find((e) => e.region == "all" && e.succ_name == "all" && e.target == d.name).value
        }
    } else {
        succ1Name = sankeyParametersArray[0].branchFilter == "all" ? sankeyParametersArray[0].regionFilter:sankeyParametersArray[0].branchFilter
        if (firstCategories.has(d.name)) {
            succ1Quantity = sankeyParametersArray[0].data.find((e) => (e.region == succ1Name || e.succ_name == succ1Name) && e.source == d.name).value
        } else {
            succ1Quantity = sankeyParametersArray[0].data.find((e) => (e.region == succ1Name || e.succ_name == succ1Name) && e.target == d.name).value
        }
            
    }

    let succ2Name
    let succ2Quantity
    if (sankeyParametersArray[1].regionFilter == "all" && sankeyParametersArray[1].branchFilter == "all") {
        succ2Name = "Toutes les régions"
        if (firstCategories.has(d.name)) {
            succ2Quantity = sankeyParametersArray[1].data.find((e) => e.region == "all" && e.succ_name == "all" && e.source == d.name).value
        } else {
            succ2Quantity = sankeyParametersArray[1].data.find((e) => e.region == "all" && e.succ_name == "all" && e.target == d.name).value
        }
        
    } else {
        succ2Name = sankeyParametersArray[1].branchFilter == "all" ? sankeyParametersArray[1].regionFilter:sankeyParametersArray[1].branchFilter
        if (firstCategories.has(d.name)) {
            succ2Quantity = sankeyParametersArray[1].data.find((e) => (e.region == succ2Name || e.succ_name == succ2Name) && e.source == d.name).value
        } else {
            succ2Quantity = sankeyParametersArray[1].data.find((e) => (e.region == succ2Name || e.succ_name == succ2Name) && e.target == d.name).value
        }
        
    }

    const succ1 = panel
    .append('div')
    .style('font-size', '16px')
    .text(succ1Name + ": " + succ1Quantity)

    const succ2 = panel
    .append('div')
    .style('font-size', '16px')
    .text(succ2Name + ": " + succ2Quantity)

}

/**
 * Displays the title of the information panel. Its color matches the color of the
 * corresponding marker on the map.
 *
 * @param {*} g The d3 selection of the SVG g element containing the title
 * @param {object} d The data to display
 * @param {*} color The color scale to select the title's color
 */
function setTitle (g, d) {
  g.text(d.name)
}

/**
 * Displays the mode in the information panel.
 *
 * @param {*} g The d3 selection of the SVG g element containing the mode
 * @param {object} d The data to display
 */
function setDistinctProduct (g, d) {
    g.text("Nombre de produits différents de cette catégorie: ")
}

