
/**
 * Generates the SVG element g which will contain the map base.
 *
 * @param {number} width The width of the graph
 * @param {number} height The height of the graph
 * @returns {*} The d3 Selection for the created g element
 */
export function generateMapG (width, height) {
  return d3.select('#map-viz')
    .select('svg')
    .append('g')
    .attr('id', 'map-g')
    .attr('width', width)
    .attr('height', height)
}

/**
 * Generates the SVG element g which will contain the map markers.
 *
 * @param {number} width The width of the graph
 * @param {number} height The height of the graph
 * @returns {*} The d3 Selection for the created g element
 */
export function generateMarkerG (width, height) {
  return d3.select('#map-viz')
    .select('svg')
    .append('g')
    .attr('id', 'marker-g')
    .attr('width', width)
    .attr('height', height)
}

/**
 * Sets the size of the SVG canvas containing the graph.
 *
 * @param {number} width The desired width
 * @param {number} height The desired height
 */
export function setCanvasSize (width, height) {
  d3.select('#map-viz').select('svg')
    .attr('width', width)
    .attr('height', height)
}

/**
 * Appends the labels for the graph.
 *
 * @param {*} g The d3 Selection of the graph's g SVG element
 */
export function appendGraphLabels (g) {
  g.append('text')
    .text('Explorez les rues pietonnes de Montréal')
    .attr('class', 'title')
    .attr('fill', '#000000')
    .attr('font-family', 'Oswald')
    .attr('font-size', 28)
    .attr('transform', 'translate(50, 50)')

  g.append('text')
    .text('Cliquez sur un marqueur pour plus d\'information.')
    .attr('class', 'title')
    .attr('fill', '#000000')
    .attr('font-family', 'Open Sans Condensed')
    .attr('font-size', 18)
    .attr('transform', 'translate(50, 85)')
}

/**
 * Initializes the div which will contain the information panel.
 */
export function initPanelDiv () {
  d3.select('#map-panel')
    .style('width', '215px')
    .style('border', '1px solid black')
    .style('padding', '10px')
    // .style('visibility', 'hidden')
}

/**
 * Initializes reset
 *
 * @param svg
 */
export function initReset (svg, zoom) {
  d3.select('#reset-map')
    .on('click', function () {
      svg
        .transition()
        .duration(500)
        .call(zoom.transform, d3.zoomIdentity.scale(1))
    })
}

export function initSwitch (viz, legend, colorScale, graphSize, margin) {
  d3.select('#switch-map')
    .on('click', function (d) {
      if (d3.select(this).html() === '<i class="fa fa-filter" style="font-size:default;"></i>&nbsp;Succursales par habitant') {
        viz.setColorScaleDomain(colorScale, 1)
        legend.initGradient(colorScale)
        legend.initLegendBar()
        legend.initLegendAxis()
        legend.draw(50, margin.top + 5, graphSize.height - 10, 15, 'url(#gradient)', colorScale, 1)
        legend.setAxisTitle('Succursales par 10 000 km2')

        d3.selectAll('#map-viz path.cancel')
          .style('fill', function (d, i) {
            if (i !== 0) {
              return colorScale(Math.log(d.properties.succ_par_km2))
            }
          })

        d3.select('.legend-title-bg')
          .attr('width', 215)

        d3.select(this).html('<i class="fa fa-filter" style="font-size:default;"></i>&nbsp;Succursales par km2')
      } else {
        viz.setColorScaleDomain(colorScale, 0)
        legend.initGradient(colorScale)
        legend.initLegendBar()
        legend.initLegendAxis()
        legend.draw(50, margin.top + 5, graphSize.height - 10, 15, 'url(#gradient)', colorScale, 0)
        legend.setAxisTitle('Succursales par 100 000 habitants')

        d3.selectAll('#map-viz path.cancel')
          .style('fill', function (d, i) {
            if (i !== 0) {
              return colorScale(d.properties.succ_par_hab)
            }
          })

        d3.select('.legend-title-bg')
          .attr('width', 255)

        d3.select(this).html('<i class="fa fa-filter" style="font-size:default;"></i>&nbsp;Succursales par habitant')
      }
    })
}

export function initZoom (child, svgSize, currentZoom) {
  d3.zoom()
    .scaleExtent([1, 200]) // This control how much you can unzoom (x0.5) and zoom (x20)
    .extent([[0, 0], [svgSize.width, svgSize.height]])
    .on('zoom', function () {
      currentZoom.val = d3.event.transform.k
      // console.log(d3.event.transform)
      child.attr('transform', d3.event.transform)
      d3
        .selectAll('#map-viz circle')
        .attr('r', 7 / d3.event.transform.k)
        .style('stroke-width', 1 / d3.event.transform.k)
      d3
        .selectAll('#map-viz circle.selected')
        .attr('r', 10 / d3.event.transform.k)
        .style('stroke-width', 1 / d3.event.transform.k)

      d3
        .selectAll('#map-viz path')
        .style('stroke-width', 1 / d3.event.transform.k)

      d3.selectAll('#map-viz .mapLabel')
        .style('font-size', 12 / d3.event.transform.k)
    })
}

/**
 * Initializes the simulation used to place the circles
 *
 * @param {object} data The data to be displayed
 * @returns {*} The generated simulation
 */
export function getSimulation (data) {
  return d3.forceSimulation(data.features)
    .alphaDecay(0)
    .velocityDecay(0.75)
    .force('collision',
      d3.forceCollide(function (d) {
        // console.log(d)
        return Math.sqrt(d.properties.succ_par_hab) * 14
      })
        .strength(1)
    )
}

/**
 * Update the (x, y) position of the circles'
 * centers on each tick of the simulation.
 *
 * @param {*} simulation The simulation used to position the cirles.
 */
export function simulate (simulation) {
  simulation.on('tick', () => {
    d3.selectAll('.marker')
      .attr('cx', (d) => d.x)
      .attr('cy', (d) => d.y)
  })
}

/**
 * Sets up the projection to be used.
 *
 * @returns {*} The projection to use to trace the map elements
 */
export function getProjection () {
  return d3.geoMercator()
    .center([-83.304095, 58.277163])
    .scale(1500)
}

/**
 * Sets up the path to be used.
 *
 * @param {*} projection The projection used to trace the map elements
 * @returns {*} The path to use to trace the map elements
 */
export function getPath (projection) {
  return d3.geoPath()
    .projection(projection)
}
